import React from 'react';
import { Box, useTheme, ButtonBase } from '@mui/material';

interface AttachmentThumbnailProps {
	link: string;
	thumbnail?: string;
	backGroundColor: string;
}

export const AttachmentThumbnail = ({
	backGroundColor,
	link,
	thumbnail
}: AttachmentThumbnailProps) => {
	const theme = useTheme();
	return (
		<Box sx={{ color: 'unset', my: 0.5 }} component="a" target="_blank" href={link}>
			<ButtonBase
				sx={{
					width: '100%',
					height: theme.spacing(24),
					p: 1,
					background: backGroundColor,
					borderRadius: 1
				}}
			>
				<Box
					sx={{
						background: theme.palette.grey['200'],
						display: 'flex',
						justifyContent: 'center',
						alignItems: 'center',
						position: 'relative',
						width: '100%',
						height: '100%'
					}}
				>
					{/* <ImageIcon sx={{ fontSize: theme.spacing(6) }} /> */}
					{thumbnail ? (
						<Box
							component="img"
							sx={{
								position: 'absolute',
								height: '100%',
								width: '100%',
								objectFit: 'cover',
								borderRadius: 1
							}}
							src={thumbnail}
						/>
					) : null}
				</Box>
				{/* <ImageListItemBar title={name} /> */}
			</ButtonBase>
		</Box>
	);
};
